import { signOut, onAuthStateChanged } from "firebase/auth";
import { auth, signInWithGoogle as googleSignIn, signInWithFacebook as facebookSignIn } from "@/firebase";

// Fun√ß√£o de login com Google
export const signInWithGoogle = () => {
  return googleSignIn();
};

// Fun√ß√£o de login com Facebook
export const signInWithFacebook = () => {
  return facebookSignIn();
};

// Fun√ß√£o para logout
export const logout = () => signOut(auth);

// Listener para monitorar o estado de autentica√ß√£o
export const subscribeToAuthChanges = (callback) => {
  onAuthStateChanged(auth, callback);
};
